*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-size: 16px;
}

body{
    background-image: url("../public/img/theme/bg/bg05.png");
    padding-bottom : 100px;
}

#header{
    background-color: white;
    z-index: 100;
    width: 100vw;
    height: max-content;
    position: fixed;
    top: 0;
    left:0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 5px 20px 5px 10px;
    box-sizing: border-box;
    border-bottom: 1px solid black;
    align-items: center;
}

#header>#imgContainer{
    width:10vw;
}
#logo{
    width: 10vw;
}
#header>#menuTogller{
    display: none;
}

#navigation-menu{
    display: flex;
    width: 40%;
    justify-content: flex-end;
    gap: 30px;
    font-family:'Courier New', Courier, monospace;
    font-size: 30px;
}

#navigation-menu> .links{
    color: black;
    text-decoration: none;
    font-size: 1.5rem;
    &:hover{
        color: blue;
    }
}

/* #navigation-menu>.link:hover{
    color: blue !important;
} */

.container{
    /* margin-top:25vh; */
    position: relative;
    padding: 0px 20px;
}

.container>div>#h2{
    
    display: flex;
    flex-wrap: wrap;
    font-family:'Courier New', Courier, monospace;
}
.container>div>#h2>span{
    font-size: 2rem;
}

.typo{
    /* position: relative; */
    display: flex;
    position: relative;
    font-size: 2rem;
    color: red;
    overflow: hidden;
    animation: typograph 2s steps(13) ;
    /* animation-delay: 0.5s; */
}

ul {
  list-style: none;
  margin-left: 0;
  padding-left: 0;
}

li {
  padding-left: 1em;
  text-indent: -1em;
}

li:before {
    content: "\21d2";
    font-size: 30px;
    font-weight:500;
    padding-right: 10px;
    taransform: translateX(-100%)
  }
@keyframes typograph{
    0%{
        width: 0ch;
    }
    100%{
        width: 13ch;
    }
}

.content{
    font-size: 1rem;
    font-family: monospace;
    margin: 3rem;
    padding: 15px;
    border-left: 1px solid grey;
}

.out{
    transition: 0.5s;
    transform: translate(-100%);
}

.in{
    transform: translateX(0);
    transition: 0.5s;
}
#cardContainer{
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
    /* padding: 1.5rem; */
    padding: 13vh 1.5rem
}
.card{
    display: flex;
    padding: 1rem;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 1rem;
    margin-bottom: 1rem;
    margin-left: 4%;
    height: calc(max(200px, 30%));
    width: calc(max(140px, 27vw));
    justify-content: space-between;
    align-items: center;
    background-color: rgba(147, 136, 136, 0.2);
}
.details{
    align-items: center;
    text-align: center;
    padding-top: 5px;
    font-size: 0.7rem;
    display: flex;
    flex-direction: column;
    gap:10px;
}
.card>img{
    width:100%;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    border-bottom: 1px solid black;

}

#productByIdContainer{
    padding-top : 13vh;
    display: flex;
    flex-direction: column;
    width: 100vw;
    justify-content: center;
    align-items: center;
}
#productByIdContainer>#images{
    width: 90%;
}
#productByIdContainer>#images>img{
    width: 100%;
    margin: 10px 0px;
    border: 1px solid black;
    padding: 30px;
    border-radius: 24px;
}

#productByIdContainer>#desc{
    padding: 30px;
    margin: 40px 0px;
    font-size: 1.5rem;
}
#productByIdContainer>#desc>h1{
    font-size: 2rem;
    margin: 10px 0px;
}

@media screen and (max-width: 550px) {
    #header>#menuTogller{
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        width: 13vw;
        /* background-color: aqua; */
    }
    #header>#menuTogller>.stick{
        width: 100%;
        height: 7px;
        background-color: grey;
        margin: 2px;
        border-radius: 7px;
        transition-duration: 1s;
    }

    #navigation-menu> .links{
        color: black;
        text-decoration: none;
        font-size: 2rem;
        &:hover{
            color: blue;
        }
    }

    .container{
        margin: 0px;
        margin-bottom: 50px;
    }

    .content{
        margin: 0;
        font-size: 1rem;
    }
    #logo{
        /* transform: scale(0.7); */
        width: 30vw;
    }
    #navigation-menu{
        display: none;
        width: 100%;
        /* display: flex; */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 0px;
        /* height: 90vh; */
        transition-duration: 1s;
        overflow: hidden;
    }
}